import { getAllCardsEstimates } from "../api/estimateApi";
import { Trello } from "../types/trello";

function roundTo2Decimals(number: number) {
    return Math.round(number * 100)/100;
}

export async function exportEstimatesToTsv(t: Trello.PowerUp.IFrame, marginPercentage: number) {
    const estimates = await getAllCardsEstimates(t, "id", "name");
    const filtered = estimates.filter(e => e.estimateMinutes > 0);

    const rows = ["Task\tHours"];

    for (const estimate of filtered)
        rows.push(`${estimate.card.name}\t${roundTo2Decimals(estimate.estimateMinutes/60)}`);

    let totalEstimateHours = 0;

    for (const estimate of filtered)
        totalEstimateHours += estimate.estimateMinutes/60;

    if (marginPercentage > 0) {
        const margin = roundTo2Decimals(totalEstimateHours * marginPercentage);
        rows.push(`margin\t${margin}`);
        totalEstimateHours += margin;
    }

    totalEstimateHours = roundTo2Decimals(totalEstimateHours);

    rows.push(`total\t${totalEstimateHours}`);

    return rows.join("\n");
}
